@import url('https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap');
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Bree Serif', serif;
}


.header{
  width: 100%;
  
  line-height: 100px;
  background-color: rgb(243, 132, 132);
  display: flex;
  justify-content: start;
  align-items: center;
  padding-left: 50px;
  color: rgb(46, 26, 26);
  box-shadow: 5px 3px 15px -5px rgba(0,0,0,0.3);
  
}

.main_note{
  width: 30rem;
  padding: 15px 10px 5px 10px;
  margin: 20px auto;
  color: #ccc;
 
}

.my_note h1{
  color: #0a0f14;
  text-transform: capitalize;
}
.my_note p{
  color: rgb(59, 50, 50);
}

form{
  min-height: 50px;
  color: #b3daff;
  padding: 15px 10px 5px 10px;
  box-shadow: 5px 3px 15px -5px rgba(0,0,0,0.3);
  border-radius: 10px;
  position: relative;
}

input{
  width: 100%;
  line-height: 30px;
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 1.5rem;
  margin: 10px 0;
  font-weight: bold;
}

textarea{
  width: 100%;
  line-height: 30px;
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 1rem;
  margin: 10px 0;
}

.MuiButton-root{
  width: 30px;
  height: 60px;
  position: absolute;
  bottom:-28px;
  left:82%;
  cursor: pointer;
  background-color: rgb(243, 132, 132) !important;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
  border-radius: 50% !important;
} 

.MuiButton-root .plus_sign{
  font-size: 45px !important;
  font-weight: 900;
  color:#f1f5f8;
  cursor: pointer;
  z-index: 222;
  background: transparent;
}

.MuiButton-root:hover{
  background-color:#ffe7b3 !important;
}

.MuiButton-root:hover .plus_sign{
  color: #fff;
  background: transparent;
}

.note{
  background: #eeddd3;
  border-radius: 7px;
  box-shadow: 0px 2px 5px #ccc;
  padding: 10px;
  width: 240px;
  margin: 16px;
  float: left;
  text-transform: capitalize;
}

.note h1{
  font-size: 1.1rem;
  margin-bottom: 6px;
}

.note p{
  font-size: 1.1rem;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.note .btn{
  position: relative;
  float: right;
  margin-right: 10px;
  color: #66adf0;
  border: none;
  width: 50px;
  height: 50px;
  background: transparent;
  cursor: pointer;
  outline: none;
  box-shadow: 3px 3px 15px -10px rgba(0,0,0,0.3);
  border-radius: 50%;
}

.deleteIcon{
  width: 0.7em;
  height: 0.7em;
}

.note .btn:hover{
  background-color: rgb(243, 132, 132);
}

.note .btn:hover .deleteIcon{
  color: rgb(99, 52, 52);
}

footer{
  width: 100%;
  position: fixed;
  bottom: 0;
  text-align: center;
  background-color: rgb(243, 132, 132);
  margin-bottom: 0px;
}

@media (max-width:768px) {
  .main_note{
      width: 90%;
  }
  .MuiButton-root .plus_sign{
    position: absolute;
  }
}